.navbar {
  position: fixed;   // Sticks at top
  top: 0;
  left: 0;
  width: 100%;
  background: #0d0d0d;
  z-index: 1000;
  transition: background 0.3s ease, box-shadow 0.3s ease;

  &.sticky {
    background: #0d0d0d;  // keep solid on scroll
    box-shadow: 0 2px 6px rgba(65, 75, 105, 0.4);
  }

  &__container {
    max-width: 1200px;
    margin: 0 auto;
    padding: clamp(0.6rem, 1vw, 1rem) clamp(1rem, 2vw, 2rem);
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__logo img {
    height: clamp(15px, 5vw, 20px);
  }

  &__menu {
    display: flex;
    gap: clamp(1rem, 3vw, 2rem);

    a {
      text-decoration: none;
      color: white;
      font-weight: bold;
      font-size: clamp(0.9rem, 1.5vw, 1.1rem);
      position: relative;

      &::after {
        content: '';
        position: absolute;
        bottom: -4px;
        left: 0;
        width: 0;
        height: 1px;
        background: skyblue;
        transition: width 0.3s ease;
      }

      &:hover::after {
        width: 100%;
      }
    }
  }

  &__icons {
    display: flex;
    gap: clamp(0.5rem, 1vw, 1rem);

    .icon {
      color: white;
      font-size: clamp(1.2rem, 2vw, 1.4rem);
      cursor: pointer;

      &:hover {
        color: white;
      }
    }
  }

  &__toggle {
    display: none;
    background: none;
    border: none;
    font-size: clamp(1.5rem, 3vw, 2rem);
    cursor: pointer;
    color: white;
    z-index: 200; /* Ensure it sits above menu */
  }
}
@media (max-width: 768px) {
  .navbar {
    &__menu {
      flex-direction: column;
      width: 100%;
      display: none;
      background: rgba(0, 0, 0, 0.95);
      padding: 1rem;
      position: absolute; /* instead of fixed fullscreen */
      top: 100%;           /* just below navbar */
      left: 0;
      z-index: 150;
      align-items: flex-start;
      justify-content: flex-start;
      transition: max-height 0.3s ease-in-out;
      max-height: 0;
      overflow: hidden;

      a {
        padding: 0.8rem 1rem;
        font-size: 1rem;
        text-align: left;
        width: 100%;
      }

      &.open {
        display: flex;
        max-height: 500px; /* enough to show items */
      }
    }

    &__toggle {
      display: block;
      position: relative; /* keep in navbar */
      top: 0;
      right: 0;
    }
  
  &__icons {
      display: none; /* hide icons in mobile */
   }
}
}