.hero {
  width: 100%;
  height: 100vh; /* full screen */
  position: relative;
}

.hero-image {
  width: 100%;
  height: 100%;
  background: url("/assets/car-background.png") center/cover no-repeat;
  position: relative;
}

.hero-content {
  position: absolute;
  top: 20%; /* ✅ locks heading position above car */
  left: 50%;
  transform: translateX(-50%);
  width: 90%;
  max-width: 900px;
  color: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Title */
.hero-title {
  font-size: clamp(2rem, 6vw, 4rem);
  font-weight: 800;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
  white-space: nowrap; /* ✅ never breaks */
}

/* Subtitle */
.hero-content h2 {
  font-size: clamp(1.2rem, 3vw, 1.8rem);
  margin-bottom: 1rem;
  white-space: nowrap; /* ✅ always in one line */
}

/* Paragraph */
.hero-content p {
  font-size: clamp(0.9rem, 2vw, 1rem);
  margin: 0 auto 2rem;
  line-height: 1.6;
  max-width: 650px; /* ✅ narrow for reverse pyramid look */
  text-align: center; /* ✅ centered pyramid style */
  hyphens: none;
  word-break: normal;
}

/* Buttons */
.hero-buttons {
  display: flex;
  justify-content: center;
  gap: clamp(0.5rem, 2vw, 1rem);
}

.order-now,
.test-drive {
  padding: clamp(0.5rem, 2vw, 0.75rem) clamp(1.25rem, 4vw, 2rem);
  border-radius: 30px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s ease;
}

.order-now {
  background: white;
  color: black;
  border: none;
  &:hover {
    background-color: #87ceeb;
    color: black;
  }
}

.test-drive {
  background: transparent;
  border: 2px solid white;
  color: white;
  &:hover {
    background-color: #87ceeb;
    color: black;
    border-color: #87ceeb;
  }
}

/* 📱 Responsive fixes */
@media (max-width: 768px) {
  .hero-content {
    top: 15%; /* move content slightly higher on small screens */
  }
  .hero-title {
    font-size: 2rem;
  }
  .hero-content h2 {
    font-size: 1.2rem;
  }
  .hero-content p {
    font-size: 0.85rem;
    max-width: 90%;
  }
}

@media (min-width: 1920px) {
  .hero-content {
    top: 25%; /* adjust for very large screens */
  }
  .hero-title {
    font-size: 4.5rem;
  }
  .hero-content h2 {
    font-size: 2rem;
  }
  .hero-content p {
    font-size: 1.2rem;
  }
}

