* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
  width: 100%;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #000;
  color: #fff;
  scroll-behavior: smooth;  
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  display: none;            
}

section {
  min-height: 100vh;
  width: 100%;
} 

