.footer {
  background: #0a0a0a;
  color: #ccc;
  padding: clamp(10px, 2vw, 15px) clamp(20px, 5vw, 60px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: clamp(0.8rem, 1.2vw, 0.9rem);

  .footer-left {
    p {
      margin: 0;
      color: #ccc;

      .brand {
        font-weight: 600;
        color: #fff;
      }
    }
  }

  .footer-right {
    display: flex;
    align-items: center;
    gap: clamp(10px, 2vw, 20px);

    a {
      color: #ccc !important;          /* force override */
      text-decoration: none !important; 
      font-size: clamp(0.8rem, 1.2vw, 0.9rem);
    }

    a:hover {
      color: #ccc !important;          /* prevent blue */
      text-decoration: underline !important;
    }

    .divider {
      width: 1px;
      height: clamp(12px, 2vw, 16px);
      background: #555;
    }

    .email {
      display: flex;
      align-items: center;
      gap: clamp(4px, 1vw, 6px);

      .icon {
        font-size: clamp(0.8rem, 1.2vw, 0.9rem);
        color: #ccc;
      }
    }
  }
}

/* Responsive Layout */
@media (max-width: 710px) {
  .footer {
    flex-direction: column;
    text-align: center;
    gap: 15px;
  }

  .footer-left {
    margin-bottom: 10px;
  }

  .footer-right {
    flex-direction: column;   // stack items
    align-items: center;
    gap: 8px;

    .divider {
      display: none; // hide divider on stacked layout
    }

    a {
      display: block; // each link full row
    }

    .email {
      justify-content: center;
    }
  }
}
