.join-astro {
  background-color: #111827;
  color: #fff;
  padding: clamp(2rem, 5vw, 4rem) clamp(1rem, 4vw, 2rem);

  &__container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: clamp(1.5rem, 5vw, 3rem);
    flex-wrap: wrap;
  }

  &__content {
    flex: 1;
    margin-left: clamp(3rem, 8vw, 7rem);
    display: flex;
    flex-direction: column;
    align-items: center; /* ✅ pyramid alignment */

    h2 {
      font-weight: 700;
      margin-bottom: clamp(1rem, 2.5vw, 1.5rem);
      line-height: 1.3;

      /* ✅ 2 lines fixed */
      font-size: clamp(1rem, 2.2vw, 1.6rem);
      text-align: center;
      max-width: 520px;

      // .astro {
      //   font-family: "Orbitron", sans-serif;
      //   font-weight: 400;
      // }
    }

    p {
      margin-bottom: clamp(1.2rem, 3vw, 2rem);
      line-height: 1.3;
      color: #d1d5db;

      /* ✅ same behavior as h2 */
      font-size: clamp(0.9rem, 1.6vw, 1rem);
      text-align: center;
      max-width: 520px;
      // display: -webkit-box;
      // -webkit-line-clamp: 2; /* ✅ force 2 lines */
      // -webkit-box-orient: vertical;
      // overflow: hidden;
    }

    .join-btn {
      padding: clamp(0.6rem, 2vw, 0.8rem) clamp(1rem, 3vw, 1.5rem);
      border: none;
      border-radius: 25px;
      background: #fff;
      color: #000;
      font-weight: 600;
      cursor: pointer;
      transition: background 0.3s;

      max-width: 280px;
      width: 100%;
      text-align: center;

      &:hover {
        background: #5aa7db;
        color: #fff;
      }
    }
  }
&__image {
  flex: 1;
  display: flex;
  justify-content: center;

  img {
    width: clamp(250px, 40vw, 400px);
    height: clamp(250px, 40vw, 400px);
    object-fit: cover;
    border-radius: 2px;

    /* 👇 Push image down */
    margin-top: 20px;  // adjust value as needed
  }
}

}

/* ✅ Mobile reset */
@media (max-width: 768px) {
  .join-astro__container {
    flex-direction: column;
    text-align: center;
  }

  .join-astro__content {
    margin-left: 0;
  }
}

/* ✅ Below 340px (still 2 lines, shrink fonts) */
@media (max-width: 340px) {
  .join-astro__content h2 {
    font-size: 0.95rem;
    line-height: 1.2;
    max-width: 280px;
  }
  .join-astro__content p {
    font-size: 0.8rem;
    line-height: 1.2;
    max-width: 260px;
    -webkit-line-clamp: 2;
  }
  .join-astro__content .join-btn {
    max-width: 240px;
  }
}

/* ✅ 340px → 545px: Force header in 1 line & shrink paragraph */
@media (min-width: 340px) and (max-width: 545px) {
  .join-astro__content h2 {
    white-space: nowrap;                     /* keep single line */
    font-size: clamp(0.65rem, 3vw, 1rem);  /* smaller header */
    line-height: 1.2;
    max-width: 100%;
    overflow: visible;
    text-overflow: unset;
    text-align: center;
    margin: 0 auto 0.5rem auto;
  }

  .join-astro__content p {
    font-size: clamp(0.65rem, 3vw, 0.9rem); /* ✅ reduced font */
    line-height: 1.2;
    text-align: center;
    margin: 0 auto 1rem auto;
    max-width: 90%;                           /* tighter fit */
  }
}

@media (min-width: 546px) and (max-width: 768px) {
  .join-astro__content h2 {
    white-space: nowrap;
    font-size: clamp(0.9rem, 2.5vw, 1.2rem);
    line-height: 1.2;
    max-width: 100%;
    overflow: visible;
    text-align: center;
    margin: 0 auto 0.5rem auto;
    padding: 2px;
  }

  .join-astro__content p {
    font-size: clamp(0.85rem, 2.5vw, 1rem);
    line-height: 1.3;
    text-align: center;
    margin: 0 auto 1rem auto;
    max-width: 95%;
  }
}

/* ✅ Desktop ≥769px: Normal 2-line left aligned */
@media (min-width: 769px) {
  .join-astro__content {
    align-items: flex-start;
    text-align: left;
    margin-left: clamp(3rem, 8vw, 7rem);
  }

  .join-astro__content h2 {
    font-size: clamp(1rem, 2.2vw, 1.6rem);
    line-height: 1.3;
    white-space: normal; /* allow wrapping */
    text-align: left;
    max-width: 520px;
  }

  .join-astro__content p {
    font-size: clamp(0.9rem, 1.6vw, 1rem);
    line-height: 1.3;
    text-align: left;
    max-width: 520px;
  }
}
/* ✅ 510px → 768px: Paragraph in 1 line, font scales up with screen */
@media (min-width: 510px) and (max-width: 768px) {
  .join-astro__content p {
    white-space: nowrap;                     
    font-size: clamp(0.75rem, 2vw, 1.1rem);  /* scales with width */
    line-height: 1.2;
    margin: 0 auto 1rem auto;
    padding: 0;                              
    max-width: 100%;                         
    overflow: hidden;
    text-align: center;
  }
}
