.find-dealer {
  position: relative;
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: clamp(1rem, 5vw, 3.75rem);
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.4);
    z-index: 0;
  }
}

.dealer-card {
  position: relative;
  z-index: 1;
  background: #fff;
  padding: 1.5rem 2rem;
  border-radius: 2px;
  width: 300px;
  height: 300px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  h2 {
    font-size: 1.6rem;
    font-weight: 700;
    margin-bottom: 0.8rem;
    color: #111;
    text-align: left;
    width: 100%;
  }

  p {
    color: #444;
    margin-bottom: 1.25rem;
    font-size: 0.95rem;
    line-height: 1.4;
    text-align: left;
    width: 100%;
  }

  .search-box {
    position: relative;
    margin-bottom: 1rem;
    width: 450px;
    height: 40px;

    .search-icon {
      position: absolute;
      left: 12px;
      top: 50%;
      transform: translateY(-50%);
      color: #888;
      font-size: 0.9rem;
    }

    input {
      width: 100%;
      height: 100%;
      padding: 0 2.5rem 0 2rem;
      border: 1px solid #ddd;
      border-radius: 30px;
      font-size: 0.95rem;
    }

    .clear-btn {
      position: absolute;
      right: 12px;
      top: 50%;
      transform: translateY(-50%);
      background: none;
      border: none;
      font-size: 1rem;
      cursor: pointer;
      color: #777;
    }
  }

  .divider {
    width: 450px;
    text-align: center;
    margin: 0.6rem 0;
    color: #888;
    font-size: 0.85rem;

    &::before,
    &::after {
      content: "";
      display: inline-block;
      width: 40%;
      height: 1px;
      background: #ddd;
      vertical-align: middle;
      margin: 0 6px;
    }
  }

  .location-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    width: 450px;
    height: 40px;
    border: none;
    border-radius: 30px;
    background: #222;
    color: #fff;
    font-size: 0.95rem;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.3s ease;

    .icon {
      font-size: 1rem;
      color: #1e90ff;
    }

    &:hover {
      background: #5aa7db;
      color: #fff;
    }
  }
}

/* ✅ Map hidden on all screen sizes */
.map-wrapper {
  display: none !important;
}

/* ✅ Mobile */
@media (max-width: 640px) {
  .find-dealer {
    justify-content: center;
    align-items: center;
    padding: 10px;
    text-align: center;
  }

  .dealer-card {
    width: 100%;
    max-width: 320px;
    height: auto;
    align-items: center;
    justify-content: center;

    h2, p {
      text-align: center;
    }

    .search-box,
    .divider,
    .location-btn {
      width: 100%;
      max-width: 220px;
      margin-left: auto;
      margin-right: auto;
    }
  }
}

/* ✅ Tablet/Desktop */
@media (min-width: 641px) {
  .dealer-card {
    width: 518px;
    height: 360px;
    align-items: flex-start;
    justify-content: flex-start;

    h2, p {
      text-align: left;
    }
  }
}
