.mission {
  position: relative;
  height: clamp(500px, 100vh, 700px); 
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    background: url("/assets/missionLogo.png") center/cover no-repeat;
    filter: brightness(2);
    z-index: 0;
  }

  .mission-overlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(
      to right at 40%,
      rgba(135, 206, 235, 0.18) 0%,
      rgba(0, 0, 0, 0.7) 90%
    );
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .mission-content {
    position: relative;
    z-index: 2;
    color: #fff;
    max-width: 800px;
    padding: 0 clamp(1rem, 4vw, 2rem);

    h2 {
      font-size: clamp(2rem, 5vw, 2.8rem);
      font-weight: 700;
      margin-bottom: clamp(1rem, 3vw, 1.5rem);
    }

    .mission-para {
      font-size: clamp(1rem, 2.5vw, 1.2rem);
      line-height: 1.5;
      margin: clamp(0.75rem, 2vw, 1rem) auto;
      max-width: 550px;

      &.first {
        margin-bottom: clamp(1rem, 3vw, 1.5rem);
      }

      &.second {
        margin-bottom: clamp(1.25rem, 3vw, 2rem);
      }
    }

    .mission-btn {
      padding: clamp(0.6rem, 2vw, 0.8rem) clamp(1.25rem, 4vw, 2rem);
      font-size: clamp(0.9rem, 2.5vw, 1rem);
      font-weight: 600;
      color: #fff;
      background-color: #87ceeb;
      border: none;
      border-radius: 50px;
      cursor: pointer;
      transition: all 0.3s ease;

      &:hover {
        background-color: #fff;
        color: #87ceeb;
      }
    }
  }
}


@media (max-width: 340px) {
  .mission-content h2 { font-size: 1.3rem; }
  .mission-content .mission-para { font-size: 0.40rem; }
  .mission-content .mission-btn { font-size: 0.75rem; padding: 0.5rem 1rem; }
}

@media (min-width: 340px) and (max-width: 640px) {
  .mission-content h2 { font-size: 1.5rem; }
  .mission-content .mission-para { font-size: 0.50rem; }
  .mission-content .mission-btn { font-size: 0.8rem; padding: 0.55rem 1.25rem; }
}

@media (min-width: 640px) and (max-width: 768px) {
  .mission-content h2 { font-size: 1.8rem; }
  .mission-content .mission-para { font-size: 0.95rem; }
  .mission-content .mission-btn { font-size: 0.85rem; padding: 0.6rem 1.4rem; }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .mission-content h2 { font-size: 2rem; }
  .mission-content .mission-para { font-size: 1rem; }
  .mission-content .mission-btn { font-size: 0.9rem; padding: 0.65rem 1.5rem; }
}

@media (min-width: 1024px) and (max-width: 1280px) {
  .mission-content h2 { font-size: 2.2rem; }
  .mission-content .mission-para { font-size: 1.05rem; }
  .mission-content .mission-btn { font-size: 0.95rem; padding: 0.7rem 1.6rem; }
}

@media (min-width: 1280px) and (max-width: 1536px) {
  .mission-content h2 { font-size: 2.4rem; }
  .mission-content .mission-para { font-size: 1.1rem; }
  .mission-content .mission-btn { font-size: 1rem; padding: 0.75rem 1.8rem; }
}

@media (min-width: 1536px) and (max-width: 1920px) {
  .mission-content h2 { font-size: 2.6rem; }
  .mission-content .mission-para { font-size: 1.15rem; }
  .mission-content .mission-btn { font-size: 1.05rem; padding: 0.8rem 2rem; }
}

@media (min-width: 1920px) and (max-width: 2133px) {
  .mission-content h2 { font-size: 2.8rem; }
  .mission-content .mission-para { font-size: 1.2rem; }
  .mission-content .mission-btn { font-size: 1.1rem; padding: 0.85rem 2.2rem; }
}




