.explore-models {
  padding: clamp(2rem, 4vw, 3rem) clamp(1rem, 3vw, 2rem);
  background: #1a222b;

  .section-header {
    text-align: left;
    margin-bottom: clamp(1.5rem, 3vw, 2.5rem);
    max-width: 600px;

    .section-title {
      font-size: clamp(1.8rem, 4vw, 2.5rem);
      font-weight: 700;
      color: #fff;
      margin-bottom: clamp(0.5rem, 1.5vw, 0.8rem);
    }

    .section-subtitle {
      font-size: clamp(0.9rem, 1.2vw, 1rem);
      color: #bbb;
      line-height: 1.6;
    }
  }

  .models-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(clamp(280px, 45vw, 340px), 1fr));
    gap: clamp(1.2rem, 3vw, 2rem);
  }

  .model-card {
    position: relative;
    height: clamp(380px, 55vw, 460px);
    perspective: 1500px;

    &::after {
      content: "";
      position: absolute;
      top: 4px;
      left: 4px;
      width: 100%;
      height: 100%;
      border: 2px solid #ffffff;
      border-radius: 0;
      z-index: 0;
    }

    .card-front,
    .card-back {
      position: absolute;
      width: 100%;
      height: 100%;
      backface-visibility: hidden;
      border-radius: 0;
      overflow: hidden;
      transition: transform 0.7s ease-in-out;
      border: 2px solid #ffffff;
      z-index: 1;
    }

    /* Front */
    .card-front {
      background: #000;
      color: #fff;
      transform: rotateY(0deg);

      .car-image {
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 0;
      }

      .card-front-overlay {
        position: relative;
        z-index: 1;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: clamp(0.8rem, 2vw, 1rem);

        h3 {
          font-size: clamp(1.1rem, 2.5vw, 1.4rem);
          font-weight: 600;
          text-align: left;
        }

        .card-footer {
          display: flex;
          justify-content: space-between;
          align-items: center;
          font-size: clamp(0.85rem, 2vw, 1rem);
          color: #fff;

          strong {
            color: #39aaf5;
          }

          .learn-more {
            font-size: clamp(0.8rem, 2vw, 0.95rem);
            opacity: 0.9;
          }
        }
      }
    }

    /* Back */
    .card-back {
      background: linear-gradient(135deg, #0a0f14, #162229);
      color: #fff;
      padding: clamp(1rem, 3vw, 1.5rem);
      transform: rotateY(180deg);
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .card-header {
        display: flex;
        justify-content: space-between;
        align-items: center;

        h3 {
          font-size: clamp(1rem, 2.5vw, 1.3rem);
          font-weight: 600;
        }

        .view-gallery {
          background: none;
          border: none;
          font-size: clamp(0.8rem, 1.5vw, 0.9rem);
          color: #39aaf5;
          cursor: pointer;
        }
      }

      p {
        font-size: clamp(0.8rem, 2vw, 0.9rem);
        color: #ddd;
        margin-bottom: 0.6rem;
        line-height: 1.4;

        a {
          color: #87ceeb;
          font-weight: 500;
          text-decoration: none;

          &:hover {
            text-decoration: underline;
          }
        }
      }

      .specs {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: clamp(0.6rem, 1.5vw, 1rem);

        .spec-item {
          display: flex;
          align-items: flex-start;
          gap: 0.5rem;

          .icon {
            font-size: clamp(1.1rem, 2.5vw, 1.4rem);
            color: #39aaf5;
          }

          strong {
            font-size: clamp(0.9rem, 2vw, 1rem);
          }

          p {
            font-size: clamp(0.7rem, 1.8vw, 0.8rem);
            color: #bbb;
          }
        }
      }

      .card-footer-back {
        display: flex;
        justify-content: space-between;
        align-items: center;

        span {
          font-size: clamp(0.9rem, 2vw, 1rem);

          strong {
            color: #39aaf5;
          }
        }

        .buttons {
          display: flex;
          gap: clamp(0.4rem, 1.5vw, 0.6rem);

          button {
            padding: clamp(0.4rem, 1.5vw, 0.5rem) clamp(0.8rem, 2vw, 1rem);
            border-radius: 20px;
            font-size: clamp(0.75rem, 1.8vw, 0.85rem);
            font-weight: 500;
            cursor: pointer;
            border: none;
            transition: all 0.3s ease;
          }

          .features-btn {
            background: #111;
            color: #fff;
            border: 1px solid #555;

            &:hover {
              background: #5aa7db;
              color: #000;
            }
          }

          .order-btn {
            background: #fff;
            color: #000;

            &:hover {
              background: #5aa7db;
              color: #fff;
            }
          }
        }
      }
    }

    &:hover .card-front {
      transform: rotateY(-180deg);
    }

    &:hover .card-back {
      transform: rotateY(0deg);
    }
  }
}

/* 📱 Responsive Breakpoints */
@media (max-width: 340px) {
  .section-title { font-size: 1.2rem; }
  .section-subtitle { font-size: 0.75rem; }
  .model-card { height: 320px; }
  .card-front-overlay h3 { font-size: 1rem; }
}

@media (min-width: 340px) and (max-width: 640px) {
  .section-title { font-size: 1.4rem; }
  .section-subtitle { font-size: 0.8rem; }
  .model-card { height: 340px; }
}

@media (min-width: 640px) and (max-width: 768px) {
  .section-title { font-size: 1.6rem; }
  .section-subtitle { font-size: 0.9rem; }
  .model-card { height: 360px; }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .section-title { font-size: 1.8rem; }
  .section-subtitle { font-size: 0.95rem; }
  .model-card { height: 400px; }
}

@media (min-width: 1024px) and (max-width: 1280px) {
  .section-title { font-size: 2rem; }
  .section-subtitle { font-size: 1rem; }
  .model-card { height: 420px; }
}

@media (min-width: 1280px) and (max-width: 1536px) {
  .section-title { font-size: 2.2rem; }
  .section-subtitle { font-size: 1.05rem; }
  .model-card { height: 440px; }
}

@media (min-width: 1536px) and (max-width: 1920px) {
  .section-title { font-size: 2.3rem; }
  .section-subtitle { font-size: 1.1rem; }
  .model-card { height: 460px; }
}

@media (min-width: 1920px) and (max-width: 2133px) {
  .section-title { font-size: 2.5rem; }
  .section-subtitle { font-size: 1.2rem; }
  .model-card { height: 480px; }
}

/* ✅ Center header, justify paragraph till 616px */
@media (max-width: 616px) {
  .explore-models {
    .section-header {
      text-align: center;
      margin: 0 auto clamp(1.5rem, 3vw, 2.5rem);
      max-width: 100%;

      .section-title {
        text-align: center;
        margin-left: auto;
        margin-right: auto;
      }

      .section-subtitle {
        text-align: center;   
        margin-left: auto;
        margin-right: auto;
        max-width: 90%;
      }
    }
  }
}
